<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__blog">
        <div class="ecom-shopify__blog-wrapper">
            <template v-if="layout === 'slider'">
                <div
                    class="ecom-shopify__blog-container ecom-swiper-container"
                    :data-items-on-row="items_on_row"
                    :data-option-swiper="optionSwiper"
                    v-html="liquid('blog')"
                />
                <div
                    v-if="isNavigation"
                    class="ecom-swiper-navigation"
                    :data-navigator-type="isCombined"
                >
                    <div class="ecom-flex-center">
                        <button
                            v-show="isArrow()"
                            class="ecom-swiper-button ecom-swiper-button-prev"
                            v-html="articles.settings.slider_prev_icon"
                        />
                        <div v-show="isPagination()" class="ecom-swiper-pagination" />
                        <button
                            v-show="isArrow()"
                            class="ecom-swiper-button ecom-swiper-button-next"
                            v-html="articles.settings.slider_next_icon"
                        />
                    </div>
                </div>
            </template>
            <div
                v-else
                class="ecom-shopify__blog-container"
                :data-items-on-row="items_on_row"
                v-html="liquid('blog')"
            />
            <div class="ecom-collection__product-loading ecom-dn">
            <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto; background: none; display: block; shape-rendering: auto;" width="48px" height="48px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                <path d="M10 50A40 40 0 0 0 90 50A40 42 0 0 1 10 50" fill="#0a0a0a" stroke="none">
                  <animateTransform attributeName="transform" type="rotate" dur="0.5434782608695652s" repeatCount="indefinite" keyTimes="0;1" values="0 50 51;360 50 51"></animateTransform>
                </path>
            </svg>
        </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
export default {
    name: 'BlogFeatured',

    mixins: [Liquid, Element, Javascript],
    vendors: ['slider_js', 'slider_css'],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            jsreactives: [
                'layout'
            ]
        };
    },
    computed: {
        articles() {
            return this.data;
        },
        attributes() {
            return this.data.settings && this.data.settings.attributes
                ? this.data.settings.attributes
                : [];
        },
        isNavigation() {
            return (
                this.data.settings?.layout === "slider" && this.data.settings?.slider_navigation_layout
            );
        },
        isCombined() {
            let navigator = this.data?.settings?.slider_navigation_layout;
            return navigator === 'neo_full' ||
                   navigator === 'neo_navigator' ||
                   navigator === 'pagination' ? 'combine' : 'classic';
        },
        css() {
            return `
                    .ecom-shopify__blog--post-informations {
                        display: inline-flex;
                        align-items: center;
                        gap: 5px;
                    }
                    .ecom-shopify__blog--post-informations-icon svg {
                        width: 16px;
                        height: 16px;
                    }
                    .ecom-swiper-pagination-bullet:only-child {
                        opacity: none
                    }
                    .ecom-flex-center, .ecom-swiper-navigation {
                        display: flex;
                        align-items: center;
                    }
                    .ecom-swiper-navigation{
                        justify-content: center
                    }
                    .ecom-shopify__blog--post-informations-author-avatar {
                        width: 50px;
                    }
                    .ecom-swiper-navigation[data-navigator-type="combine"]{
                        justify-content: center
                    }
                    .ecom-shopify__blog--post-thumbnail {
                        display: flex;
                        position: relative;
                        overflow: hidden;
                    }

                    .ecom-shopify__blog--post-thumbnail img{
                            object-fit: cover;
                            display: block;
                            max-width: 100%;
                            top: 0;
                            left: 0;
                            height: 100% !important;
                            width: 100%;
                    }
                    .ecom-shopify__blog--post-tags ul li{
                        display:inline-flex;
                    }
                    .ecom-shopify__blog--post-tags ul {
                        display: flex;
                        flex-wrap:wrap;
                        gap: 12px;
                        margin: 0;
                        padding: 0;
                    }
                    /*.ecom-shoify__blog-pagination {
                        margin: 0;
                        padding: 0;
                        text-align: center;
                        list-style-type: none;
                        display: flex;
                        justify-content: center;
                        gap: 24px;
                        align-items: center;
                        margin-top: 32px;
                        color: #000;
                    }*/

                    .ecom-collection__pagination-navigation{
                        list-style: none;
                        display: flex;
                        gap: 10px;
                        justify-content: center;
                    }
                    .ecom-collection__pagination-navigation li a {
                        color: inherit;
                    }
                    .ecom-paginate-action {
                        display:flex;
                        gap:5px;
                    }
                    .ecom-prev,
                    .ecom-next,
                    .ecom-prev span,
                    .ecom-next span{
                        display:flex;
                    }
                    .ecom-paginate-action svg{
                        width:24px;
                        height:24px;
                    }
                    .ecom-shopify__blog--post-link{
                        text-decoration:none;
                        width:100%;
                        display:flex;
                    }
                    .ecom-pagination-item {
                        display: flex;
                    }
                    a.ecom-pagination-item:not(.ecom-paginate-action) {
                        text-decoration:inherit
                    }
                    .ecom-paginate-action{
                        text-decoration:none
                    }
                    .ecom-pagination-item.ecom-button-active{
                        pointer-events:none;
                    }
                    .ecom-swiper-controls:after
                    {
                        content:'';
                        display:none
                    }
                    .ecom-shopify__blog-wrapper {
                        display: flex;
                        flex-direction: column;
                    }
                    .ecom-shopify__blog-wrapper .ecom-shopify__blog-container{
                        width: 100%
                    }
                    .ecom-shopify__blog-wrapper .ecom-swiper-button-next:after,
                    .ecom-shopify__blog-wrapper .ecom-swiper-button-prev:after {
                        content: none;
                    }
                    .ecom-shopify__blog-wrapper .ecom-swiper-navigation[data-navigator-type="combine"] .ecom-swiper-button-next,
                    .ecom-shopify__blog-wrapper .ecom-swiper-navigation[data-navigator-type="combine"] .ecom-swiper-button-prev {
                        position: static;
                        margin: 0;
                    }
                    .ecom-shopify__blog-wrapper .ecom-swiper-button-next,
                    .ecom-shopify__blog-wrapper .ecom-swiper-button-prev {
                        border: 0;
                        background: transparent;
                        width: auto;
                        height: auto;
                        padding: 5px;
                        color: #444
                    }
                    .ecom-shopify__blog-wrapper .ecom-swiper-pagination {
                        position: relative;
                        display: flex;
                        flex-wrap: wrap;
                        align-items: center
                    }
                    .ecom-swiper-container-horizontal>.ecom-swiper-pagination-bullets{
                        width:auto;
                    }
                    .ecom-shopify__blog--post-title {
                        font-size: inherit;
                    }

                    .ecom-shopify__blog--post-link img{
                        object-fit: cover;
                        max-width: 100%;
                        object-position: center center;
                        transition: opacity .4s cubic-bezier(.25, .46, .45, .94);
                    }
                    .ecom-shopify__blog--post-group-4 {
                        flex-wrap: wrap;
                    }
                    .ecom-shopify__blog--post-group-4 > *{
                        display: inline-flex;
                        align-items: center;
                    }
                    .ecom-swiper-pagination-bullet{
                        opacity:1 !important;
                    }
                    .ecom-swiper-pagination-bullets.ecom-swiper-pagination{
                        display:flex;
                        width:100%;
                    }
                    .ecom-shopify__blog--post-link h2{
                        width:100%;
                    }
                    .ecom-shopify__blog--post.ecom-shopify__blog-vertical {
                        display: flex;
                        gap: 15px;
                    }
                    @media (max-width: 767px) {
                        .ecom-shopify__blog--post.ecom-shopify__blog-vertical {
                            flex-direction: column;
                        }
                        .ecom-shopify__blog--post .ecom-shopify__blog--post-thumbnail--img {
                            order: 0 !important;
                        }
                    }
                    .ecom-doing-load-blog .ecom-shopify__blog-container{
                        display: none;
                    }
                    .ecom-doing-load-blog .ecom-collection__product-loading{
                        display: block;
                    }
                    `;
        },

        javascript() {
            return function () {
                if (this.settings.layout === 'slider') {
                    let $el = this.$el;
                    if(!$el) return;
                    let    $container = $el.querySelector('.ecom-shopify__blog-container'),
                        optionSwiper = $container && $container.dataset.optionSwiper;
                        optionSwiper = JSON.parse(optionSwiper);
                        optionSwiper.pagination = {
                            el: $el.querySelector('.ecom-swiper-pagination'),
                            type: 'bullets',
                            clickable: true
                        }
                        optionSwiper.navigation = {
                            nextEl: $el.querySelector('.ecom-swiper-button-next'),
                            prevEl: $el.querySelector('.ecom-swiper-button-prev')
                        }
                    new window.EComSwiper($container, optionSwiper);

                }
                //pagination
                if(this.settings.use_ajax){
                    if(this.isLive){
                        const $el = this.$el;
                        if(!$el) return;
                        const main_wrapper = $el.querySelector(".ecom-shopify__blog-wrapper");
                        const handleClickNumberPages = function(e){
                            e.preventDefault();
                            const url = this.dataset.get;
                            const wrapper = this.closest('.ecom-sections[data-section-id]');
                            if(!url || !wrapper || !wrapper.dataset.sectionId) return;
                            const section_id = wrapper.dataset.sectionId;
                            const fetch_url = `${url}&section_id=${section_id}`;
                            handleLoadBlog(fetch_url,wrapper);
                            main_wrapper.scrollIntoView();
                        }
                        const btnNum = $el.querySelectorAll('.ecom-pagination-item');
                        btnNum.forEach((element)=>{
                            element.addEventListener('click',handleClickNumberPages);
                        });
                        const handleLoadBlog = function(fetch_url, wrapper){
                            const loadBlog = async function(url){
                                const response = await fetch(url,{
                                    method: 'GET',
                                    cache: 'no-cache',
                                    headers: {
                                        'Content-Type': 'text/html'
                                    },
                                });
    
                                return response.text();
                            }
                            wrapper.classList.add('ecom-doing-load-blog');
                            loadBlog(fetch_url).then(function(data){
                                const oldBlogs = $el.querySelectorAll(".ecom-shopify__blog--post");
                                oldBlogs.forEach(function(element){
                                    element.remove();
                                });
                                const dom = document.createElement('div');
                                dom.innerHTML = data;
                                const blogs_item = dom.querySelector('.ecom-shopify__blog--posts');
                                if(!blogs_item) return;
                                const wrapper_blogs = wrapper.querySelector('.ecom-shopify__blog--posts');
                                const pagination = wrapper.querySelector('.ecom-pagination-navigation.ecom-collection__pagination-navigation');
                                while(blogs_item.firstChild){
                                    wrapper_blogs.appendChild(blogs_item.firstChild);
                                }
                                blogs_item.parentNode.removeChild(blogs_item);
                                const paginate = dom.querySelector('.ecom-pagination-navigation.ecom-collection__pagination-navigation');
                                if(paginate){
                                    pagination.innerHTML = paginate.innerHTML;
                                    const btnNum = $el.querySelectorAll('.ecom-pagination-item');
                                    btnNum.forEach((element)=>{
                                        element.addEventListener('click',handleClickNumberPages);
                                    });
                                }
                            })
                            .finally(function(){
                                wrapper.classList.remove('ecom-doing-load-blog')
                            })
                        }
                    }
                }
            };
        },


        items_on_row() {
            return this.data.settings && this.data.settings.items_on_row
                ? this.data.settings.items_on_row
                : 4;
        },

        layout() {
            return this.data.settings && this.data.settings.layout
                ? this.data.settings.layout
                : 'grid';
        },

        liquids() {
            return {
                blog: {
                    code: `
                        {% assign tmp_block = blog %}
                        {%- capture blog_handle -%}${this.selected_blog}{%-  endcapture-%}
                        {%- if blog_handle -%}
                            ${this.data.template === 'featured' ? `{%- assign blog = blogs[blog_handle]-%}` : ``}
                        {%- endif -%}
                        {% if blog != null %}
                            {%- capture limit -%}${this.data.settings?.limit? this.data.settings.limit : 20 }{%-endcapture-%}

                            {% assign limit = limit | plus: 0 %}
                            {% assign tmp_article = article %}
                            ${this.data.template === 'related' ? `{% assign limit = limit | plus: 1 %}`:''}
                            {% assign count = 1 %}
                            {%- paginate blog.articles by limit -%}


                                <div class="${this.layout === 'slider' ? 'ecom-swiper-wrapper' : 'ecom-grid' } ecom-shopify__blog--posts" style="${this.layout === 'slider' ? '' : 'display:grid'}">
                                {% for article in blog.articles %}
                                {% if count > limit %}{% break%}{% endif %}
                                {% assign count =  count | plus: 1%}
                                ${this.data.template === 'related' ? `{% if tmp_article  and article.id == tmp_article.id %}{% continue %}{% endif %}` : ``}
                                    <div class="ecom-shopify__blog--post${this.layout === 'slider' ? ' ecom-swiper-slide' : ''}${this.is_horizontal_content ? ' ecom-shopify__blog-vertical' : ``}">
                                        ${this.is_horizontal_content
                                            ?
                                                `
                                                    <div class="ecom-shopify__blog--post-thumbnail--img">
                                                        <div class="ecom-shopify__blog--post-thumbnail ecom-image-align {% if article.image == blank %} ecom-shopify__blog--post-no-thumbnail{% endif %}">
                                                            {%- if article.image -%}
                                                            {%- liquid
                                                                assign featured_media_aspect_ratio = article.featured_media.aspect_ratio

                                                                if article.featured_media.aspect_ratio == nil
                                                                    assign featured_media_aspect_ratio = 1
                                                                endif
                                                            -%}
                                                                    <a style="text-decoration:none" href="{{article.url}}" class="ecom-shopify__blog--post-link ecom-image-default"  data-blog-handle="{{article.handle}}">
                                                                    <img srcset="
                                                                        {%- if article.image.src.width >= 165 -%}{{ article.image.src | img_url: '165x' }} 165w,{%- endif -%}
                                                                        {%- if article.image.src.width >= 360 -%}{{ article.image.src | img_url: '360x' }} 360w,{%- endif -%}
                                                                        {%- if article.image.src.width >= 533 -%}{{ article.image.src | img_url: '533x' }} 533w,{%- endif -%}
                                                                        {%- if article.image.src.width >= 720 -%}{{ article.image.src | img_url: '720x' }} 720w,{%- endif -%}
                                                                        {%- if article.image.src.width >= 940 -%}{{ article.image.src | img_url: '940x' }} 940w,{%- endif -%}
                                                                        {%- if article.image.src.width >= 1066 -%}{{ article.image.src | img_url: '1066x' }} 1066w{%- endif -%}"
                                                                        src="{{ article.image.src | img_url: '533x' }}"
                                                                        sizes="(min-width: 1100px) 535px, (min-width: 750px) calc((100vw - 130px) / 2), calc((100vw - 50px) / 2)"
                                                                        alt="{{ article.image.src.alt | escape }}"
                                                                        width="{{ article.image.width }}"
                                                                        height="{{ article.image.height }}"
                                                                        loading="lazy"
                                                                    >
                                                                </a>
                                                                {%- endif -%}
                                                        </div>
                                                    </div>
                                                    <div class="ecom-shopify__blog--posts-details">
                                                `
                                            : ``
                                        }
                                ${this.attributes
                                    .map((attr) => {
                                        switch (attr.type) {
                                            case 'title':
                                                var tag = attr.settings?.tag ? attr.settings?.tag: 'h2';
                                                return `<a href="{{article.url }}" class="ecom-shopify__blog--post-link"  data-blog-handle="{{article.handle}}"><${tag} class="ecom-shopify__blog--post-title">{{article.title}}</${tag}></a>`;
                                            case 'thumbnail':
                                                if(!this.is_horizontal_content) {
                                                    return `
                                                        <div class="ecom-shopify__blog--post-thumbnail--img">
                                                            <div class="ecom-shopify__blog--post-thumbnail ecom-image-align {% if article.image == blank %} ecom-shopify__blog--post-no-thumbnail{% endif %}">
                                                                {%- if article.image -%}
                                                                {%- liquid
                                                                    assign featured_media_aspect_ratio = article.featured_media.aspect_ratio

                                                                    if article.featured_media.aspect_ratio == nil
                                                                        assign featured_media_aspect_ratio = 1
                                                                    endif
                                                                -%}
                                                                        <a style="text-decoration:none" href="{{article.url }}" class="ecom-shopify__blog--post-link ecom-image-default"  data-blog-handle="{{article.handle}}">
                                                                        <img srcset="
                                                                            {%- if article.image.src.width >= 165 -%}{{ article.image.src | img_url: '165x' }} 165w,{%- endif -%}
                                                                            {%- if article.image.src.width >= 360 -%}{{ article.image.src | img_url: '360x' }} 360w,{%- endif -%}
                                                                            {%- if article.image.src.width >= 533 -%}{{ article.image.src | img_url: '533x' }} 533w,{%- endif -%}
                                                                            {%- if article.image.src.width >= 720 -%}{{ article.image.src | img_url: '720x' }} 720w,{%- endif -%}
                                                                            {%- if article.image.src.width >= 940 -%}{{ article.image.src | img_url: '940x' }} 940w,{%- endif -%}
                                                                            {%- if article.image.src.width >= 1066 -%}{{ article.image.src | img_url: '1066x' }} 1066w{%- endif -%}"
                                                                            src="{{ article.image.src | img_url: '533x' }}"
                                                                            sizes="(min-width: 1100px) 535px, (min-width: 750px) calc((100vw - 130px) / 2), calc((100vw - 50px) / 2)"
                                                                            alt="{{ article.image.src.alt | escape }}"
                                                                            width="{{ article.image.width }}"
                                                                            height="{{ article.image.height }}"
                                                                            loading="lazy"
                                                                        >
                                                                    </a>
                                                                    {%- endif -%}
                                                            </div>
                                                        </div>
                                                    `;
                                                } else {
                                                    return '';
                                                }
                                            case 'excerpt':
                                                var limit = attr.settings?.limit_words ?? 12;
                                                return `
                                                <div class="ecom-shopify__blog--post-excerpt">
                                                    {{ article.excerpt_or_content | strip_html | escape | truncatewords: ${limit}}}
                                                </div>
                                            `;
                                            case 'author':
                                                return `
                                                <div class="ecom-shopify__blog--post-author">
                                                    {% assign article_author = article.author %}
                                                   ${this.lang('By {{article_author}}','post_by_author',{article_author: 'article_author' })}
                                                </div>
                                            `;
                                            case 'category':
                                                return `
                                                    <div class="ecom-shopify__blog--post-category">
                                                        <a href="{{blog.url}}" alt="{{blog.title}}">{{blog.title}}</a>
                                                    </div>
                                                `;
                                            case 'tags':
                                                return `
                                                <div class="ecom-shopify__blog--post-tags ecom-shopify__blog--post-informations">
                                                    {% assign tmpl =  '' %}
                                                    {% if  template.suffix  %}
                                                        {% assign tmpl = '?view=' | append: template.suffix %}
                                                    {% endif %}
                                                    {% if article.tags.size %}
                                                        <ul>
                                                            {% for tag in article.tags %}
                                                                <li>
                                                                    <a href="/blogs/{{blog.handle}}/tagged/{{tag | handleize }}{{tmpl}}">{{ tag }}</a>
                                                                </li>
                                                            {% endfor %}
                                                        </ul>
                                                    {% endif %}
                                                </div>
                                            `;

                                            case 'meta':
                                                var format = attr?.settings?.date_format ?? 'date';
                                                var layout = attr?.settings?.meta_layout ?? 'one';
                                                // eslint-disable-next-line no-var
                                                var templates = {
                                                    one: [
                                                        'author',
                                                        'published_at',
                                                        'comments_count'
                                                    ],
                                                    two: [
                                                        'published_at',
                                                        'author',
                                                        'comments_count'
                                                    ],
                                                    three: [
                                                        'comments_count',
                                                        'author',
                                                        'published_at'
                                                    ],
                                                    four:[
                                                        'group_meta_4',
                                                        'comments_count'
                                                    ],
                                                    five:[
                                                        'comments_count'
                                                    ]
                                                };
                                                if(attr?.settings?.hide_author) {
                                                    templates[layout] = templates[layout].filter(type => type != 'author');
                                                }
                                                if(attr?.settings?.hide_time) {
                                                    templates[layout] = templates[layout].filter(type => type != 'published_at');
                                                }
                                                if(attr?.settings?.hide_comment_count) {
                                                    templates[layout] = templates[layout].filter(type => type != 'comments_count');
                                                }

                                                // eslint-disable-next-line no-var
                                                var metas = {
                                                    author: `<div class="ecom-shopify__blog--post-author ecom-shopify__blog--post-informations">
                                                        {% assign article_author = article.author %}
                                                        ${attr?.settings?.icon_author
                                                            ? `
                                                                <span class="ecom-shopify__blog--post-informations-icon">${attr?.settings?.icon_author}</span>
                                                            `
                                                            : ''
                                                        }
                                                        ${!attr?.settings?.icon_author && attr?.settings?.avatar_type != 'off'
                                                            ? `
                                                                ${attr?.settings?.avatar_type == 'shopify'
                                                                    ? `
                                                                        {%- if article.user.image -%}
                                                                            <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                <div class="ecom-image-default">
                                                                                    <img src="{{ article.user.image | img_url: 'master' }}" alt="{{ article.author }}">
                                                                                </div>
                                                                            </div>
                                                                        {%- else -%}
                                                                            <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                <div class="ecom-image-default">
                                                                                    ${attr?.settings?.image_author?.url
                                                                                        ? `
                                                                                            <img src="${attr?.settings?.image_author?.url}" alt="{{ article.author }}"/>
                                                                                        `
                                                                                        : ``
                                                                                    }
                                                                                </div>
                                                                            </div>
                                                                        {%- endif -%}
                                                                    `
                                                                    : ''
                                                                }
                                                                ${attr?.settings?.avatar_type == 'gavatar'
                                                                    ? `
                                                                        {%- if article.user.email -%}
                                                                            <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                <div class="ecom-image-default">
                                                                                    <img src="https://www.gravatar.com/avatar/{{ article.user.email | downcase | md5 }}" alt="{{ article.author }}">
                                                                                </div>
                                                                            </div>
                                                                        {%- endif -%}
                                                                    `
                                                                    : ''
                                                                }
                                                            `
                                                            : ''
                                                        }
                                                        ${this.lang(`${attr?.settings?.hide_by_text ? '' : 'By '}{{article_author}}`,'post_by_author',{article_author: 'article_author' })}
                                                    </div>`,
                                                    published_at: `<div class="ecom-shopify__blog--post-published-at ecom-shopify__blog--post-informations">
                                                        {% assign published_at = article.published_at | time_tag: format: '${format}'  %}
                                                        ${attr?.settings?.icon_time
                                                            ? `
                                                                <span class="ecom-shopify__blog--post-informations-icon">${attr?.settings?.icon_time}</span>
                                                            `
                                                            : ''
                                                        }
                                                        <span>${this.lang(`${attr?.settings?.hide_in_text ? '' : ' in '}{{published_at}}`, 'post_published_at',{ published_at: 'published_at'})}</span>
                                                    </div>`,
                                                    comments_count: `
                                                            <div class="ecom-shopify__blog--post-comments_count ecom-shopify__blog--post-informations">
                                                                {% if article.comments_enabled? %}
                                                                    ${attr?.settings?.icon_comment
                                                                        ? `
                                                                            <span class="ecom-shopify__blog--post-informations-icon">${attr?.settings?.icon_comment}</span>
                                                                        `
                                                                        : ''
                                                                    }
                                                                    {% if article.comments_count == 1%}
                                                                        <span>
                                                                            ${this.lang(attr?.settings?.comment_count_text ??'','comment_count_text',{comment_count: 'article.comments_count'})}
                                                                        </span>
                                                                    {% else %}
                                                                        <span>
                                                                            ${this.lang(attr?.settings?.comments_count_text ??'','comments_count_text',{comments_count:'article.comments_count'})}
                                                                        </span>
                                                                            {% endif %}
                                                                    {% endif %}
                                                                    </div>`
                                                    ,
                                                    group_meta_4:`
                                                        <div class="ecom-shopify__blog--post-group-4 ecom-flex ecom-al_center ${attr?.settings?.layout4_meta_aligment}">
                                                            ${attr?.settings?.hide_author ? '' :
                                                                `<div class="ecom-shopify__blog--post-author ecom-shopify__blog--post-informations">
                                                                    {% assign article_author = article.author %}
                                                                    ${attr?.settings?.icon_author
                                                                        ? `
                                                                            <span class="ecom-shopify__blog--post-informations-icon">${attr?.settings?.icon_author}</span>
                                                                        `
                                                                        : ''
                                                                    }
                                                                    ${!attr?.settings?.icon_author && attr?.settings?.avatar_type != 'off'
                                                                        ? `
                                                                            ${attr?.settings?.avatar_type == 'shopify'
                                                                                ? `
                                                                                    {%- if article.user.image -%}
                                                                                        <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                            <div class="ecom-image-default">
                                                                                                <img src="{{ article.user.image | img_url: 'master' }}" alt="{{ article.author }}">
                                                                                            </div>
                                                                                        </div>
                                                                                    {%- else -%}
                                                                                        <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                            <div class="ecom-image-default">
                                                                                                ${attr?.settings?.image_author?.url
                                                                                                    ? `
                                                                                                        <img src="${attr?.settings?.image_author?.url}" alt="{{ article.author }}"/>
                                                                                                    `
                                                                                                    : ``
                                                                                                }
                                                                                            </div>
                                                                                        </div>
                                                                                    {%- endif -%}
                                                                                `
                                                                                : ''
                                                                            }
                                                                            ${attr?.settings?.avatar_type == 'gavatar'
                                                                                ? `
                                                                                    {%- if article.user.email -%}
                                                                                        <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                            <div class="ecom-shopify__blog--post-informations-author-avatar">
                                                                                                <div class="ecom-image-default">
                                                                                                    <img src="https://www.gravatar.com/avatar/{{ article.user.email | downcase | md5 }}" alt="{{ article.author }}">
                                                                                                </div>
                                                                                            </div>
                                                                                        </div>
                                                                                    {%- endif -%}
                                                                                `
                                                                                : ''
                                                                            }
                                                                        `
                                                                        : ''
                                                                    }
                                                                    ${this.lang(`${attr?.settings?.hide_by_text ? '' : 'By '}{{article_author}}`,'post_by_author',{article_author: 'article_author' })}
                                                                </div>`
                                                            }
                                                             ${attr?.settings?.hide_time ? '' :
                                                                `<div class="ecom-shopify__blog--post-published-at ecom-shopify__blog--post-informations">
                                                                    {% assign published_at = article.published_at | time_tag: format: '${format}'  %}
                                                                    ${attr?.settings?.icon_time
                                                                        ? `
                                                                            <span class="ecom-shopify__blog--post-informations-icon">${attr?.settings?.icon_time}</span>
                                                                        `
                                                                        : ''
                                                                    }
                                                                    <span>${this.lang(`${attr?.settings?.hide_in_text ? '' : ' in '}{{published_at}}`, 'post_published_at',{ published_at: 'published_at'})}</span>
                                                                </div>`
                                                            }
                                                        </div>
                                                    `
                                                };

                                                var html = '';
                                                for (var tmp of templates[layout]) {
                                                    html += metas[tmp];
                                                }
                                                return html;

                                            case 'link':
                                                return `
                                                    <div class="ecom-shopify__blog--post-link-wrapper">
                                                        <a href="{{article.url }}" class="ecom-shopify__blog--post-link-btn ecom-flex ecom-al_center ecom-fl_center"  data-blog-handle="{{article.handle}}">
                                                            ${attr?.settings?.text ? `<span>${attr?.settings?.text}</span>` : ``}
                                                            ${attr?.settings?.icon ? `<span class="ecom__element--button-icon ecom-flex ecom-al_center ecom-fl_center">${attr?.settings?.icon}</span>` : ``}
                                                        </a>
                                                    </div>
                                                `;

                                            default:
                                                return '';
                                            }
                                        })
                                        .join('')}
                                        ${this.is_horizontal_content ? '</div>' : ``}
                                    </div>
                                    {% endfor %}
                                </div>
                                ${
                                    this.data.template !== 'featured' && this.data?.settings?.enable_pagination && this.data.settings?.layout === 'grid'
                                        ? `
                                 {%- if paginate.pages > 1 -%}
                                    <nav role="navigation">
                                        <ol class="ecom-pagination-navigation ecom-collection__pagination-navigation">
                                            {%- if paginate.previous -%}
                                                <li class="ecom-prev" style="${
                                                this.data.settings.pagination_style === 'block'? 'margin-right:auto': ''}">
                                                    <a class="ecom-pagination-item ecom-paginate-action" data-get="{{ paginate.previous.url }}" href="{{ paginate.previous.url }}">
                                                    ${this.data.settings.number_type === 'icon' || this.data.settings.number_type === 'text_icon'
                                                    ? `<span class="ecom-paginate-action--icon">${this.data.settings.icon_prev_page || ''}</span>`
                                                    : ''
                                                    }
                                                    ${this.data.settings.number_type !== 'icon'? this.lang(this.data.settings.text_prev_page,'prev_page'): ''}
                                                    </a>
                                                </li>
                                            {%- else -%}
                                                <li class="ecom-pagination-item ecom-prev ecom-paginate-action disabled" style="${
                                                this.data.settings.pagination_style === 'block'? 'margin-right:auto': ''}">
                                                    ${ this.data.settings.number_type === 'icon' || this.data.settings.number_type ==='text_icon'
                                                    ?  `<span class="ecom-paginate-action--icon">${this.data.settings.icon_prev_page || ''}</span>`
                                                    : ''
                                                    }
                                                    ${this.data.settings.number_type !== 'icon'? this.lang(this.data.settings.text_prev_page,'prev_page'): ''}
                                                </li>
                                            {%- endif -%}

                                            {%- for part in paginate.parts -%}
                                                {%- if part.is_link -%}
                                                    <li>
                                                        <a class="ecom-pagination-item" data-get="{{part.url}}" href="{{ part.url }}" title="{{ part.title }}">
                                                            {{ part.title }}
                                                        </a>
                                                    </li>
                                                {%- else -%}
                                                    {%- if part.title == paginate.current_page -%}
                                                    <li class="ecom-pagination-item ecom-button-active" aria-current="page">
                                                        {{ part.title }}
                                                    </li>
                                                    {%- else -%}
                                                    <li class="ecom-pagination-item">
                                                        {{ part.title }}
                                                    </li>
                                                    {%- endif -%}
                                                {%- endif -%}
                                            {%- endfor -%}

                                            {%- if paginate.next -%}
                                                <li class="ecom-next" style="${
                                                this.data.settings.pagination_style === 'block'? 'margin-left:auto': ''}">
                                                    <a class="ecom-pagination-item ecom-paginate-action" data-get="{{ paginate.next.url }}" href="{{ paginate.next.url }}">

                                                        ${this.data.settings.number_type !=='icon' ? this.lang(this.data.settings?.text_next_page,'next_page'): '' }

                                                        ${this.data.settings.number_type ==='icon' ||this.data.settings.number_type === 'text_icon'
                                                        ? `<span class="ecom-paginate-action--icon">${this.data.settings.icon_next_page || ''}</span>`
                                                        : ''
                                                        }
                                                    </a>
                                                </li>
                                                {%- else -%}
                                                <li class="ecom-pagination-item ecom-next ecom-paginate-action ecom-collection__pagination--disabled" style="${
                                                this.data.settings.pagination_style === 'block'? 'margin-left:auto': ''}">
                                                    ${this.data.settings.number_type !== 'icon' ? this.lang(this.data.settings.text_next_page,'next_page') : ''}
                                                    ${this.data.settings.number_type === 'icon' || this.data.settings.number_type === 'text_icon'
                                                        ? `<span class="ecom-paginate-action--icon">${this.data.settings.icon_next_page || ''}</span>`
                                                        : ''
                                                    }
                                                </li>
                                            {%- endif -%}
                                        </ol>
                                    </nav>
                                {%- endif -%}
                                `
                                        : ''
                                }
                            {%- endpaginate -%}
                        {% else %}
                            ${ this.exporting? '<div>No posts yet!</div>': '<div>Please select the blog in settings</div>'}
                        {% endif %}
                        {% assign blog = tmp_block %}
                        {% assign article = tmp_article %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-4">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-big"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-big"></div>
                                </div>
                            </div>
                            <div class="ecom-skeleton-col-4">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-big"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-big"></div>
                                </div>
                            </div>
                            <div class="ecom-skeleton-col-4">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-big"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType() {
            if(this.data.template === 'featured' )
            {
                return {};
            }
            if(this.data.template === 'featured' || this.data.template === 'related')
            {
                return {
                    shopify_type: 'article'
                };
            }
            return {
                shopify_type: 'blog'
            };
        },

        selected_blog() {
            return this.data && this.data.settings && this.data.settings.blog
                ? this.data.settings.blog.value
                : '';
        },
        is_horizontal_content() {
            return this.data && this.data.settings && this.data.settings.content_layout == 'horizontal'
        },

        settings() {
            let settings = [
                {
                    group_title: 'General',
                    params: [
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'layout',
                            options: {
                                type: 'dropdown',
                                values: {
                                    grid: 'Grid',
                                    slider: 'Slider'
                                },
                                default: false
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Content layout',
                            name: 'content_layout',
                            options: {
                                type: 'dropdown',
                                values: {
                                    vertical: 'Vertical',
                                    horizontal: 'Horizontal'
                                },
                                default: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Image thumbnail position',
                            name: 'image_thumbnail_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
