                            css:{
                                selector:' .ecom-shopify__blog--post-thumbnail--img',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'limit',
                            label: 'Maximum articles to show',
                            options: {
                                min: 1,
                                max: 30
                            }
                        },
                        {
                            type: 'group',
                            label: 'Select Attributes to show',
                            name: 'attributes',
                            params: [
                                {
                                    type: 'title',
                                    name: 'Title',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'popup',
                                            label: 'Heading Tag',
                                            name: 'tag',
                                            options: {
                                                type: 'dropdown',
                                                values: {
                                                    h1: 'Heading h1',
                                                    h2: 'Heading h2',
                                                    h3: 'Heading h3',
                                                    h4: 'Heading h4',
                                                    h5: 'Heading h5',
                                                    h6: 'Heading h6'
                                                }
                                            }
                                        }
                                    ]
                                },
                                {
                                    type: 'thumbnail',
                                    name: 'Featured image',
                                    max: 1,
                                    settings: []
                                },

                                {
                                    type: 'excerpt',
                                    name: 'Post excerpt',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'number',
                                            name: 'limit_words',
                                            label: 'Maximum words to show',
                                            options: {
                                                min: 5,
                                                max: 200
                                            }
                                        }
                                    ]
                                },
                                {
                                    type: 'tags',
                                    name: 'Post tags',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'Display all the tags for the article'
                                        }
                                    ]
                                },
                                {
                                    type: 'category',
                                    name: 'Category',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'Display category for the article'
                                        }
                                    ]
                                },
                                {
                                    type: 'meta',
                                    name: 'Post meta',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'toggle',
                                            label: 'Hide Author',
                                            name: 'hide_author',
                                            options: {
                                                values:{
                                                    on:{
                                                        label: 'Yes',
                                                        value: true
                                                    },
                                                    off: {
                                                        label: 'No',
                                                        value: false
                                                    }
                                                }
                                            }
                                        },
                                        {
                                            type: 'toggle',
                                            label: "Hide 'by' text",
                                            name: 'hide_by_text',
                                            options: {
                                                values:{
                                                    on:{
                                                        label: 'Yes',
                                                        value: true
                                                    },
                                                    off: {
                                                        label: 'No',
                                                        value: false
                                                    }
                                                }
                                            }
                                        },
                                        {
                                            type: 'toggle',
                                            label: 'Hide Time',
                                            name: 'hide_time',
                                            options: {
                                                values:{
                                                    on:{
                                                        label: 'Yes',
                                                        value: true
                                                    },
                                                    off: {
                                                        label: 'No',
                                                        value: false
                                                    }
                                                }
                                            }
                                        },
                                        {
                                            type: 'toggle',
                                            label: "Hide 'in' text",
                                            name: 'hide_in_text',
                                            options: {
                                                values:{
                                                    on:{
                                                        label: 'Yes',
                                                        value: true
                                                    },
                                                    off: {
                                                        label: 'No',
                                                        value: false
                                                    }
                                                }
                                            }
                                        },
                                        {
                                            type: 'toggle',
                                            label: 'Hide Comment Count',
                                            name: 'hide_comment_count',
                                            options: {
                                                values:{
                                                    on:{
                                                        label: 'Yes',
                                                        value: true
                                                    },
                                                    off: {
                                                        label: 'No',
                                                        value: false
                                                    }
                                                }
                                            }
                                        },
                                        {
                                            type: 'line'
                                        },
                                        {
                                            type: 'picker',
                                            label: 'Icon Time',
                                            name: 'icon_time',
                                            options: {
                                                type: 'icon',
                                                layout: 'grid',
                                                output: 'value',
                                                multiple: false,
                                                simple: false
                                            }
                                        },
                                        {
                                            type: 'picker',
                                            label: 'Icon Comment',
                                            name: 'icon_comment',
                                            options: {
                                                type: 'icon',
                                                layout: 'grid',
                                                output: 'value',
                                                multiple: false,
                                                simple: false
                                            }
                                        },
                                        {
                                            type: 'picker',
                                            label: 'Icon Author',
                                            name: 'icon_author',
                                            options: {
                                                type: 'icon',
                                                layout: 'grid',
                                                output: 'value',
                                                multiple: false,
                                                simple: false
                                            }
                                        },
                                        {
                                            type: 'paragraph',
                                            content: 'Or'
                                        },
                                        {
                                            type: 'popup',
                                            name: 'avatar_type',
                                            label: 'Avatar',
                                            options: {
                                                type: 'dropdown',
                                                default: false,
                                                preview: 'title',
                                                values: {
                                                    off: 'Off',
                                                    shopify: 'Shopify avatar',
                                                    gavatar: 'G-Avatar',
                                                }
                                            }
                                        },
                                        {
                                            type: 'picker',
                                            label: 'Image Author default',
                                            name: 'image_author',
                                            description: 'Image default will display if user not avatar',
                                            options: {
                                                responsive: false,
                                                type: 'image',
                                                editAlt: false,
                                                visible: {
                                                    keep_data: true,
                                                    condition: (data) => {
                                                        return data['avatar_type'] == 'shopify';
                                                    }
                                                }
                                            }
                                        },
                                        {
                                            type: 'choose',
                                            label: 'Icon, Image position',
                                            name: 'icon_position',
                                            options: {
                                                type: 'align-x',
                                                values: [-1, 1]
                                            },
                                            css:{
                                                selector:' .ecom-shopify__blog--post-informations-icon',
                                                properties:{
                                                    order:''
                                                }
                                            }
                                        },
                                        {
                                            type: 'number',
                                            label: 'Icon, Image spacing',
                                            name: 'spacing',
                                            options: {
                                                units: {
                                                    px: {
                                                        min: 0,
                                                        max: 200
                                                    }
                                                }
                                            },
                                            css:{
                                                selector:' .ecom-shopify__blog--post-informations',
                                                properties:{
                                                    gap:''
                                                }
                                            }
                                        },
                                        {
                                            type: 'line'
                                        },
                                        {
                                            type: 'popup',
                                            name: 'meta_layout',
                                            label: 'Meta layout',
                                            options: {
                                                type: 'dropdown',
                                                default: false,
                                                values: {
                                                    one: 'Layout 1',
                                                    two: 'Layout 2',
                                                    three: 'Layout 3',
                                                    four: 'Layout 4',
                                                    five: 'Layout 5',
                                                }
                                            }
                                        },
                                        {
                                            name: "layout4_meta_aligment",
                                            label: "Meta layout 4 alignment",
                                            type: "choose",
                                            options: {
                                                type: "text-align",
                                                values: [
                                                    "ecom-fl_left",
                                                    "ecom-fl_center",
                                                    "ecom-fl_right"
                                                ],
                                                visible: {
                                                    keep_data: false,
                                                    condition: (data) => {
                                                        return data['meta_layout'] == 'four'
                                                    }
                                                }
                                            },
                                            css: {
                                                selector: " .ecom-shopify__blog--post-group-4",
                                                properties: {
                                                    "text-align": ""
                                                }
                                            }
                                        },
                                        {
                                            type: 'text',
                                            name: 'comment_count_text',
                                            label: 'Comment text count',
                                            placeholder: '{{comment_count}} comment',
                                            description: 'Use {{comment_count}} to show comment count'
                                        },
                                        {
                                            type: 'text',
                                            name: 'comments_count_text',
                                            label: 'Many comments text count',
                                            placeholder: '{{comments_count}} comments',
                                            description:
                                                'Use {{comments_count}} to show comments count'
                                        },
                                        {
                                            type: 'popup',
                                            name: 'date_format',
                                            label: 'Date format?',
                                            options: {
                                                type: 'dropdown',
                                                values: {
                                                    default:
                                                        'Monday, December 31, 2018 at 1:00 pm -0500',
                                                    abbreviated_date: 'Aug 08, 1994',
                                                    basic: '12/31/2021',
                                                    date: 'December 31, 2018',
                                                    date_at_time: 'December 31, 2018 at 1:00 pm',
                                                    on_date: 'on Dec 31, 2018'
                                                }
                                            }
                                        }
                                    ]
                                },
                                {
                                    type: 'link',
                                    name: 'Read more link',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'text',
                                            label: 'Read more text',
                                            name: 'text',
                                            options: {
                                                placeholder: 'Read more'
                                            }
                                        },
                                        {
                                            type: 'picker',
                                            label: 'Icon',
                                            name: 'icon',
                                            options: {
                                                type: 'icon',
                                                layout: 'grid',
                                                output: 'value',
                                                multiple: false,
                                                simple: false
                                            }
                                        }
                                    ]
                                },
                            ]
                        },
                    ]
                },
                {
                    group_title: 'Grid settings',
                    options: {
                        visible: {
                            keep_data: false,
                            condition: data => data['layout'] === 'grid'
                        }
                    },
                    params: [
                        {
                            type: 'number',
                            label: 'Items on row',
                            name: 'slider_items',
                            value: 3,
                            options: {
                                responsive: true,
                                min: 1,
                                max: 6,
                                step: 1,
                                slider: true
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--posts.ecom-grid',
                                properties: {
                                    'grid-template-columns': 'repeat(%value%,1fr)'
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Space between',
                            name: 'slider_spacing',
                            options: {
                                responsive: true,
                                min: 0,
                                max: 64,
                                slider: true,
                                input: true
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--posts.ecom-grid',
                                properties: {
                                    'gap': '%value%px',
                                }
                            }
                        }
                    ]
                },
                {
                    group_alias: 'swiper',
                    options: {
                        options: {
                            keep_data: false,
                            visible: data => {
                                return data['layout'] == 'slider';
                            }
                        }
                    },
                    modify: {
                        remove: {
                            name: ['line_pagination','title_pagination','slider_pagination_style', 'slider_spacing_row']
                        }
                    }
                }
            ];
            if (this.data.template === 'blog') {

                settings.push({
                    group_alias: 'pagination:settings',
                    options:{
                        group_name:'',
                        options:{
                            visible: {
                                keep_data: false,
                                condition: (data) => {
                                    return data['layout'] != 'slider';
                                }
                            }
                        }
                    },
                    modify: {
                        params: [
                            {   
                                position: 10,
                                fields: [
                                    {
                                        type: 'toggle',
                                        label: 'Use ajax pagination',
                                        name: 'use_ajax',
                                        options: {
                                            values:{
                                                on:{
                                                    label: 'Yes',
                                                    value: true
                                                },
                                                off: {
                                                    label: 'No',
                                                    value: false
                                                }
                                            }
                                        }
                                    }
                                ]
                                
                            },
                        ]
                    }
                });
            }
            if(this.data.template === 'featured')
            {
                settings[0].params.splice(0,0,{
                    type:'picker',
                    name: 'blog',
                    label: 'Select a blog',
                    options: {
                        type: 'blog',
                        layout: 'list',
                        multiple: false
                    }
                });
            }
            return settings;
        },



        optionSwiper() {
            return this.$helpers.optionSwiper(this.data.settings);
        },

        default() {
            return {
                "settings": {
                    "slider_items": 3,
                    "limit": 3,
                    "show_title": true,
                    "slider_items__mobile": 1,
                    "slider_items__tablet": 2,
                    "slider_spacing": 30,
                    "slider_navigation_layout": "classic_full",
                    "slider_prev_icon": "<svg xmlns=\"http: //www.w3.org/2000/svg\" width=\"52\" height=\"52\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-chevron-left\"><polyline points=\"15 18 9 12 15 6\"></polyline></svg>",
                    "slider_next_icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"52\" height=\"52\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-chevron-right\"><polyline points=\"9 18 15 12 9 6\"></polyline></svg>",
                    "slider_effect": "slide",
                    "slider_speed": 500,
                    "layout": "grid",
                    "attributes": [
                        {
                            "type": "thumbnail"
                        },
                        {
                            "type": "title",
                            "settings": {
                                "tag": "h2"
                            }
                        },
                        {
                            "type": "excerpt",
                            "settings": {
                                "limit_words": 12
                            }
                        },
                        {
                            "type": "meta",
                            "settings": {
                                "meta_layout": "four",
                                "comments_count_text": "{{comments_count}} comments",
                                "comment_count_text": "{{comment_count}} comment",
                                "date_format": "abbreviated_date"
                            }
                        },
                        {
                            "type": "tags",
                            "settings": {}
                        },
                        {
                            "type": "link",
                            "settings": {
                                "text": "Read more"
                            }
                        }
                    ],
                    "text_prev_page": "Prev",
                    "text_next_page": "Next",
                    "enable_pagination": true,
                    "pagination_style": "inline",
                    "grid-column-gap": "10px",
                    "number_type": "icon",
                    "icon_prev_page": "<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"52\" height=\"52\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-arrow-left\"><line x1=\"19\" y1=\"12\" x2=\"5\" y2=\"12\"></line><polyline points=\"12 19 5 12 12 5\"></polyline></svg>",
                    "icon_next_page": "<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"52\" height=\"52\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-arrow-right\"><line x1=\"5\" y1=\"12\" x2=\"19\" y2=\"12\"></line><polyline points=\"12 5 19 12 12 19\"></polyline></svg>",
                },
                "style": {
                    "blog_articles_img": {
                        "justify-content": "center",
                        "imageOpacitynormalmode": 1,
                        "imageOpacityhovermode": 1,
                        "spacing": {
                            "margin": {
                                "bottom": "24px"
                            }
                        },
                        "tab": "normal"
                    },
                    "blog_articles_title": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "6px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            }
                        },
                        "tab": "normal",
                        "textTypography": {
                            "font-size": "18px"
                        }
                    },
                    "blog_articles_description": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "8px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            }
                        }
                    },
                    "blog_articles_datetime": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            }
                        },
                        "textTypography": {
                            "font-style": "italic"
                        }
                    },
                    "blog_articles_author": {
                        "spacing": {
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "margin": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "8px"
                            }
                        },
                        "textTextAlign": "left",
                        "textTypography": {
                            "font-weight": "300",
                            "font-style": "italic"
                        }
                    },
                    "blog_articles_comment_count": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            }
                        }
                    },
                    "blog_articles_pagination": {
                        "buttonAlignment": "center",
                        "spacing": {
                            "margin": {
                                "top": "20px"
                            }
                        },
                        "buttonColornormalmode": "#111827",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "rgba(17, 24, 39, 0.1)"
                            }
                        },
                        "buttonColorhovermode": "#111827",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "rgba(17, 24, 39, 0.2)"
                            }
                        },
                        "buttonColoractivemode": "#ffffff",
                        "buttonBackgroundactivemode": {
                            "classic": {
                                "background-color": "#111827"
                            }
                        },
                        "padding": {
                            "left": "20px",
                            "top": "8px",
                            "bottom": "8px",
                            "right": "20px"
                        },
                        "tab": "normal"
                    },
                    "blog_articles_tags": {
                        "tab": "normal",
                        "spacing": {
                            "margin": {
                                "top": "0px"
                            }
                        }
                    },
                    "slider_arrow": {
                        "navtab": "normal",
                        "tab": "normal",
                        "panigationSpacing": {
                            "margin": {
                                "top": "10px",
                                "right": "6px",
                                "left": "0px",
                                "bottom": "0px"
                            },
                            "padding": {
                                "top": "6px",
                                "left": "6px",
                                "bottom": "6px",
                                "right": "6px"
                            }
                        },
                        "panigationColornormalmode": "#948f8f",
                        "panigationColoractivemode": "#d43b3b",
                        "navigatorFontSize": "30px",
                        "iconTransform": 0
                    },
                    "blog_articles_link_btn": {
                        "iconFontSize": "16px",
                        "icon_spacing": "10px",
                        "content_alignment": "flex-start"
                    }
                }
            }
        }
    },
    watch: {
        optionSwiper: {
            deep: true,
            handler: function () {
                this.articles.refresh = this.$helpers.randid();
            }
        }
    },
    methods: {
        check(val){
            let check = false;
            for(let i = 0; i < this.data.settings.attributes?.length; i++){
                if(this.data.settings.attributes[i].type == val){
                    check = true;
                }
            }
            return check;
        },
        isArrow() {
            return this.data.settings?.slider_navigation_layout === 'neo_full' ||
                    this.data.settings?.slider_navigation_layout === 'neo_navigator' ||
                    this.data.settings?.slider_navigation_layout === 'classic_full' ||
                    this.data.settings?.slider_navigation_layout === 'navigator';
        },
        isPagination() {
            return this.data.settings?.slider_navigation_layout === 'neo_full' ||
                    this.data.settings?.slider_navigation_layout === 'classic_full' ||
                    this.data.settings?.slider_navigation_layout === 'pagination';
        },
        style() {
            let style = [
                this.check('thumbnail') ? {
                    group_alias:'image',
                    options:{
                        selector: ' .ecom-shopify__blog--post-thumbnail--img .ecom-image-default',
                        group_name: 'blog_articles_img',
                        group_title: 'Image'
                    },
                    modify:{
                        params:[
                            {
                                position:0,
                                fields:{
                                    alias:'justify-content',
                                    options:{
                                        label:'Alignment',
                                        css:{
                                            selector:'root .ecom-image-align'
                                        }
                                    }
                                }
                            },
                            {
                                position:20,
                                fields:{
                                    alias:'spacing',
                                    options:{
                                        css:{
                                            selector:'root .ecom-image-align'
                                        }
                                    }
                                }
                            }
                        ]
                    }
                } : null,
                this.check('title') ? {
                    group_alias:'text:hover',
                    options:{
                        selector: ' .ecom-shopify__blog--post-link:not(.ecom-image-default) > .ecom-shopify__blog--post-title',
                        group_name: 'blog_articles_title',
                        group_title: 'Title'
                    },
                    modify:{
                        params:{
                            position:30,
                            fields:{
                                alias:'spacing'
                            }
                        }
                    }
                } : null,
                this.check('excerpt') ? {
                    group_alias:'text:spacing',
                    options:{
                        selector: ' .ecom-shopify__blog--post-excerpt',
                        group_name: 'blog_articles_description',
                        group_title: 'Description'
                    }
                } : null,
                this.check('tags') ? {
                    group_alias:'button',
                    options:{
                        selector: ' .ecom-shopify__blog--post-tags li a',
                        group_name: 'blog_articles_tags',
                        group_title: 'Tags'
                    },
                     modify:{
                        params:[
                            {
                                position:0,
                                fields:{
                                    alias:'justify-content',
                                    options:{
                                        label:'Alignment',
                                        css:{
                                            selector:'root .ecom-image-align'
                                        }
                                    }
                                }
                            }

                        ]
                    }
                } : null,

            ].filter(x => x);
            if(this.check('meta')) {
                style.push(
                {
                    group_alias: 'text:spacing',
                    options:{
                        selector: ' .ecom-shopify__blog--post-published-at',
                        group_name: 'blog_articles_datetime',
                        group_title: 'Datetime'
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options:{
                        selector: ' .ecom-shopify__blog--post-author',
                        group_name: 'blog_articles_author',
                        group_title: 'Author'
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options:{
                        selector: ' .ecom-shopify__blog--post-category a',
                        group_name: 'blog_articles_category',
                        group_title: 'Category'
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options:{
                        selector: ' .ecom-shopify__blog--post-comments_count span',
                        group_name: 'blog_articles_comment_count',
                        group_title: 'Comment Count'
                    }
                },
                {
                    group_alias:'icon',
                    options:{
                        group_name: 'icon',
                        group_title: 'Meta Icon',
                        selector: ' .ecom-shopify__blog--post-informations-icon',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias:'image',
                    options:{
                        selector: ' .ecom-shopify__blog--post-informations-author-avatar',
                        group_name: 'blog_meta_img',
                        group_title: 'Meta Avatar'
                    },
                    modify: {
                        remove: {
                            label: 'Width',
                            length: 1
                        },
                        params: {
                            position: 1,
                            fields: [
                            {
                                type: "number",
                                name: "imageWidth",
                                label: "Image Width",
                                options: {
                                    responsive: true,
                                    reset: true,
                                    units: {
                                        "%": {
                                            min: 0,
                                            max: 100
                                        },
                                        px: {
                                            min: 0,
                                            max: 1000
                                        },
                                        vw: {
                                            min: 0,
                                            max: 100
                                        }
                                    }
                                },
                                css: {
                                    important: true,
                                    properties: {
                                        width: ""
                                    }
                                }
                            },
                            ]
                        }
                    }
                }
                )
            }

            if(this.check('link')) {
                style.push(
                {
                    group_alias: 'button',
                    options:{
                        selector: ' .ecom-shopify__blog--post-link-btn',
                        group_name: 'blog_articles_link_btn',
                        group_title: 'Read more link'
                    },
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: [
                                    {
                                        type: 'paragraph',
                                        content: '** Icon **',
                                        name: 'title_icon',
                                    },
                                    {
                                        name: "iconFontSize",
                                        label: "Size",
                                        type: "number",
                                        options: {
                                            responsive: true,
                                            units: {
                                                px: {
                                                    min: 0,
                                                    max: 300
                                                }
                                            }
                                        },
                                        css: {
                                            selector: " svg",
                                            properties: {
                                                height: "",
                                                width: ""
                                            }
                                        }
                                    },
                                    {
                                        name: "iconTransform",
                                        label: "Rotate",
                                        type: "number",
                                        options: {
                                            responsive: true,
                                            min: 0,
                                            max: 360
                                        },
                                        css: {
                                            selector: " svg",
                                            properties: {
                                                transform: "rotate(%value%deg)"
                                            }
                                        }
                                    },
                                    {
                                        type: 'choose',
                                        label: 'Icon position',
                                        name: 'icon_position',
                                        options: {
                                            type: 'align-x',
                                            values: [-1, 1],
                                        },
                                        css:{
                                            selector:' .ecom__element--button-icon',
                                            properties:{
                                                order:''
                                            }
                                        }
                                    },
                                    {
                                        type: 'number',
                                        label: 'Icon spacing',
                                        name: 'icon_spacing',
                                        options: {
                                            units: {
                                                px: {
                                                    min: 0,
                                                    max: 200
                                                }
                                            },
                                        },
                                        css:{
                                            properties:{
                                                gap:''
                                            }
                                        }
                                    },
                                    {
                                        type:'line'
                                    },
                                    {
                                        type: 'paragraph',
                                        content: "** Link **",
                                        name: 'title_link',
                                    },
                                    {
                                        alias:'align-items',
                                        options:{
                                            label:'Content alignment',
                                            name: 'content_alignment',
                                            css:{
                                                properties:{
                                                    'justify-content':''
                                                }
                                            }
                                        }
                                    }
                                ]
                            }
                        ]
                    }
                },
                )
            }

            if (this.data.template !== 'featured' && this.data.settings.enable_pagination && this.data.settings.layout !== 'slider') {
                style.push({
                    group_alias: 'pagination',
                    options:{
                        selector: ' .ecom-pagination-navigation',
                        group_name: 'blog_articles_pagination',
                    },
                    modify: this.data.settings.pagination_style !== 'block' ? {
                        params: [
                            {
                                position:1,
                                fields: {
                                    type: 'choose',
                                    name: 'buttonAlignment',
                                    label: 'Alignment',
                                    options: {
                                        responsive: true,
                                        type: 'text-align',
                                        values: ['flex-start', 'center', 'flex-end']
                                    },
                                    css: {
                                        properties: {
                                            display: 'flex',
                                            'justify-content': ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 4,
                                fields: {
                                    type: 'choose',
                                    name: 'page_txt_alignment_horizontal',
                                    label: 'Text alignment<small>(horizontal)</small>',
                                    options: {
                                        responsive: true,
                                        type: 'align-x-full',
                                        values: ['left', 'center', 'right']
                                    },
                                    css: {
                                        selector: ' .ecom-pagination-item',
                                        properties: {
                                            'text-align': '',
                                            'justify-content': ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 4,
                                fields: {
                                    type: 'choose',
                                    name: 'page_txt_alignment_vertical',
                                    label: 'Text alignment<small>(vertical)</small>',
                                    options: {
                                        responsive: true,
                                        type: 'align-y-full',
                                        values: ['start', 'center', 'end']
                                    },
                                    css: {
                                        selector: ' .ecom-pagination-item',
                                        properties: {
                                            'align-items': ''
                                        }
                                    }
                                }
                            },
                        ]
                    }: null
                });
            }
            let checkNavPagination = [];
            if(this.isArrow()){
                checkNavPagination.push({
                    title:'Navigator',
                    type:'swiper:nav'
                });
            }
            if(this.isPagination()){
                checkNavPagination.push({
                    title:'Pagination',
                    type:'swiper:pagination'
                });
            }
            let modifyNavigation = {};
            if(this.isCombined === 'combine') {
                modifyNavigation = {
                    params: [
                        {
                            alias: 'spacing',
                            options: {
                                name: 'spacingNavigation',
                                css: {
                                    selector: ' .ecom-swiper-navigation'
                                }
                            }
                        },
                        {
                            type: 'line'
                        }
                    ],
                    remove: {
                        name: 'justify-content'
                    }
                };
            }
            if (checkNavPagination.length) {
                style.push({
                    group_alias: checkNavPagination,
                    options:{
                        group_title: 'Navigation',
                        group_name: 'slider_arrow',
                        selector: ' .ecom-shopify__blog-wrapper',
                    },
                    modify: modifyNavigation
                });
            }



            return style;
        }
    }
};
</script>
